import Airtable from "airtable";
import Team from "../../domain/Team";
import TeamId from "../../domain/TeamId";
import TeamName from "../../domain/TeamName";
import ChoozrId from "../../domain/ChoozrId";
export default class RESTTeamOutputAdapter {
    constructor() {
        this.recordToTeam = (record) => {
            return new Team(new TeamId(record.id), new TeamName(record.get("name")), new ChoozrId(record.get("id (from Choozr)")));
        };
    }
    async getTeamsMembersCount() {
        return Promise.resolve([]);
    }
    async createTeamWith(choozrId, teamName, loginParameters) {
        const airtable = new Airtable({ apiKey: loginParameters.apiKey }).base(loginParameters.appId);
        const records = await airtable("Team").create([
            {
                "fields": {
                    "name": teamName.value,
                    "Choozr": [
                        choozrId.value
                    ]
                }
            },
        ]);
        return records.map(this.recordToTeam)[0];
    }
}
